import mutations from "../mutations"

const { SET_USER_INFO, SET_CHILDREN_INFO } = mutations;

export default {
    namespaced: true,
    state: {
        user: {
            username: '',
            age: ''
        },
        children: []
    },
    getters: {
        getUser: (state) => state.user,
        getChildren: (state) => state.children,
    },
    mutations: {
        [SET_USER_INFO](state, userInfo) {
            state.user = userInfo;

        },
        [SET_CHILDREN_INFO](state, childrenInfo) {
            state.children = childrenInfo;
        },
    },
    actions: {
        setUserInfo({ commit }, { user, children }) {
            commit(SET_USER_INFO, user);
            commit(SET_CHILDREN_INFO, children);
        }
    },
}