<template>
  <section class="user">
    <div class="container user__container">
      <!-- USER -->
      <form action="#" class="form form-user">
        <h2 class="title form-title">Персональные данные</h2>
        <label>
          <input
            class="form__input form-user__input"
            type="text"
            placeholder=" "
            v-model.trim="user.username"
          />
          <span class="form__label form-user__label">Имя</span>
        </label>
        <label>
          <input
            class="form__input form-user__input"
            type="number"
            placeholder=" "
            v-model.trim="user.age"
          />
          <span class="form__label form-user__label">Возраст</span>
        </label>
      </form>
      <!-- CHILDREN -->
      <form action="#" class="form form-children">
        <div class="form-children__heading-block">
          <h2 class="title form-title form-children__title">Дети (макс.5)</h2>
          <button
            v-if="childreCountIsMax"
            class="btn btn__add"
            @click.prevent="addChildren()"
          >
            <svg
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M5.13332 10.8556C4.50125 10.8555 3.98887 11.3679 3.98887 12C3.98887 12.6321 4.50126 13.1445 5.13332 13.1445L10.8554 13.1445L10.8554 18.8668C10.8554 19.4989 11.3678 20.0113 11.9999 20.0113C12.632 20.0113 13.1444 19.4989 13.1444 18.8668L13.1443 13.1445L18.8667 13.1445C19.4988 13.1445 20.0112 12.6321 20.0112 12C20.0112 11.3679 19.4988 10.8556 18.8667 10.8556L13.1443 10.8556L13.1443 5.13338C13.1443 4.50132 12.632 3.98893 11.9999 3.98893C11.3678 3.98893 10.8554 4.50131 10.8554 5.13338L10.8554 10.8556L5.13332 10.8556Z"
                fill="#01A7FD"
              />
            </svg>
            <span>Добавить ребенка</span>
          </button>
        </div>

        <fieldset
          class="fieldset-children"
          v-for="(children, index) in childrens"
          :key="index"
        >
          <label>
            <input
              class="form__input form-children__input"
              type="text"
              placeholder=" "
              v-model.trim="childrens[index].childrenName"
            />
            <span class="form__label form-children__label">Имя</span>
          </label>

          <label>
            <input
              class="form__input form-children__input"
              type="number"
              placeholder=" "
              v-model.trim="childrens[index].childrenAge"
            />
            <span class="form__label form-children__label">Возраст</span>
          </label>

          <button class="btn btn__delete" @click="deleteChildren(index)">
            Удалить
          </button>
        </fieldset>
      </form>
      <button class="btn btn__save" @click.prevent="setUser">Сохранить</button>
    </div>
  </section>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  mounted() {
    this.user = this.getUser;
    this.childrens = this.getChildren;
  },
  data() {
    return {
      user: {
        username: "",
        age: "",
      },
      childrens: [],
    };
  },
  methods: {
    ...mapActions("usersInfo", ["setUserInfo"]),
    addChildren() {
      this.childrens.push({
        childrenName: "",
        childrenAge: "",
      });
    },
    deleteChildren(index) {
      this.childrens.splice(index, 1);
    },
    setUser() {
      this.setUserInfo({
        user: this.user,
        children: this.childrens,
      });
      this.$router.push({ name: "PreviewUser" });
    },
  },
  computed: {
    ...mapGetters("usersInfo", ["getUser", "getChildren"]),
    childreCountIsMax() {
      return this.childrens.length < 5;
    },
  },
};
</script>

